export default {
	data() {
		return {
			navIndex:0,
			current_typeId:'',
			current_page:1,
			triggered:false,
			more:"more",
			NewsTabs:[],
			NewsList:[],
		};
	},
	onLoad(){
		this.getNewsTabs();
	},
	onPullDownRefresh(){
		console.log("Refresh");
		this.NewsList=[];
		this.current_page=1;
		this.more="more";
		this.getNewsList(this.current_typeId,this.current_page);
		setTimeout(function(){
			uni.stopPullDownRefresh()
		},1000)
	},
	onReachBottom(){
		this.more="loading";
		var that=this;
		
		this.getNewsList(this.current_typeId,++this.current_page,function(res){
			//console.log(res.data.data.length);
			if(res.data.data.length==0){
				that.more="noMore";
			}else{
				that.more="more";
			}
			
			
		});
		
	},
	methods:{
		// async getinfo () {
		// 	const res = await this.$myRuquest({
		// 		url:'/holiday/single/20181121?'+this.myKey
		// 	})
		// },
		async getNewsTabs(){
			const res = await this.$myRuquest({
				url:'/news/types?'+this.myKey
			})
			this.NewsTabs=res.data.data;
			this.current_typeId=this.NewsTabs[0].typeId;
			this.current_page=1
			this.NewsList=[];
			this.getNewsList(this.current_typeId,this.current_page);
			
		},
		async getNewsList(typeId,page,callBack){
			const res = await this.$myRuquest({
				url:'/news/list?'+this.myKey+'&typeId='+typeId+'&page='+page
			})
		
			this.NewsList=[...this.NewsList,...res.data.data];
			callBack && callBack(res)
		},
		changeTabs(index,data){
			this.navIndex=index;
			this.current_typeId=data.typeId;
			this.current_page=1;
			this.NewsList=[];
			this.more="more";
			this.getNewsList(this.current_typeId,this.current_page);
		},
		//跳转到新闻详情
		ToNewsDetail(id){
			uni.navigateTo({
			    url: '../news-detail/news-detail?'+'newsId='+id
			});
			
		}
		
	}
}
